import React, { useState, useEffect } from 'react';
import { useAuth } from "../../common/context/AuthContext";
import PromoterNavbar from '../components/PromoterNavbar';
import { 
  UnifiedBackground, 
  UnifiedCard, 
  UnifiedButton,
  useScrollAnimation,
  SharedStyles
} from "../../common/components/SharedTheme";
import { supabase } from "../../common/services/supabaseClient"
import { Search, Plus, Edit, Trash2, Users, CreditCard, Mail, Phone } from 'lucide-react';
import { SkeletonFullPage } from '../components/skeletons';
import UnifiedCustomerForm from '../../common/components/UnifiedCustomerForm';
import PaymentManager from '../../common/components/PaymentManager';

function PromoterCustomers() {
  const { user } = useAuth();
  useScrollAnimation();

  // State management - unified with AdminCustomers
  const [customers, setCustomers] = useState([]);
  const [filteredCustomers, setFilteredCustomers] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [statusFilter, setStatusFilter] = useState('');
  const [showModal, setShowModal] = useState(false);
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [editingCustomer, setEditingCustomer] = useState(null);
  const [deletingCustomer, setDeletingCustomer] = useState(null);
  const [loading, setLoading] = useState(true);
  const [showPaymentManager, setShowPaymentManager] = useState(false);
  const [selectedCustomerForPayments, setSelectedCustomerForPayments] = useState(null);
  // Promoter-specific states
  const [availablePins, setAvailablePins] = useState(0);

  // Load customers and pins on component mount
  useEffect(() => {
    loadCustomers();
    loadPromoterPins();
  }, []);

  // Filter and search logic - unified with AdminCustomers
  useEffect(() => {
    let filtered = customers || [];

    // Search filter
    if (searchTerm) {
      filtered = filtered.filter(customer => {
        const name = (customer?.name || '').toLowerCase();
        const email = (customer?.email || '').toLowerCase();
        const phone = (customer?.phone || '').toLowerCase();
        const customerId = (customer?.customer_id || '').toLowerCase();
        const searchLower = (searchTerm || '').toLowerCase();
        return name.includes(searchLower) || 
               email.includes(searchLower) || 
               phone.includes(searchLower) ||
               customerId.includes(searchLower);
      });
    }

    // Status filter
    if (statusFilter) {
      filtered = filtered.filter(customer => 
        (customer?.status || 'active') === statusFilter
      );
    }

    setFilteredCustomers(filtered);
  }, [customers, searchTerm, statusFilter]);

  // Load customers from Supabase - role-based filtering for promoters
  const loadCustomers = async () => {
    if (!user?.id) return;
    
    try {
      setLoading(true);
      console.log('üîÑ Loading customers for promoter:', user.id);

      // Get customers assigned to this promoter (role-based access)
      const { data, error } = await supabase
        .from('profiles')
        .select(`
          id,
          name,
          email,
          phone,
          customer_id,
          state,
          city,
          pincode,
          address,
          investment_plan,
          parent_promoter_id,
          status,
          created_at,
          updated_at
        `)
        .eq('role', 'customer')
        .eq('parent_promoter_id', user.id)
        .order('created_at', { ascending: false });
      
      if (error) {
        console.error('Error loading customers:', error);
        setCustomers([]);
        setFilteredCustomers([]);
        throw error;
      }
      
      console.log(`‚úÖ Loaded ${data?.length || 0} customers for promoter`);
      setCustomers(data || []);
      setFilteredCustomers(data || []);
    } catch (error) {
      console.error('Error loading customers:', error);
      setCustomers([]);
      setFilteredCustomers([]);
    } finally {
      setLoading(false);
    }
  };

  // Load promoter pins
  const loadPromoterPins = async () => {
    if (!user?.id) return;
    
    try {
      const { data, error } = await supabase
        .from('profiles')
        .select('pins')
        .eq('id', user.id)
        .single();
        
      if (error) {
        console.error('Error loading promoter pins:', error);
        return;
      }
      
      setAvailablePins(data?.pins || 0);
    } catch (error) {
      console.error('Error loading promoter pins:', error);
      setAvailablePins(0);
    }
  };

  // Modal handlers - unified with AdminCustomers
  const openCreateModal = () => {
    setEditingCustomer(null);
    setShowModal(true);
  };

  const openEditModal = (customer) => {
    setEditingCustomer(customer);
    setShowModal(true);
  };

  const closeModal = () => {
    setShowModal(false);
    setEditingCustomer(null);
  };

  const openDeleteModal = (customer) => {
    setDeletingCustomer(customer);
    setShowDeleteModal(true);
  };

  const closeDeleteModal = () => {
    setShowDeleteModal(false);
    setDeletingCustomer(null);
  };

  // Payment management handlers
  const openPaymentManager = (customer) => {
    setSelectedCustomerForPayments(customer);
    setShowPaymentManager(true);
  };

  const closePaymentManager = () => {
    setShowPaymentManager(false);
    setSelectedCustomerForPayments(null);
  };

  // Toggle customer status
  const toggleCustomerStatus = async (customer) => {
    try {
      const newStatus = customer.status === 'active' ? 'inactive' : 'active';
      
      const { error } = await supabase
        .from('profiles')
        .update({ status: newStatus })
        .eq('id', customer.id);
      
      if (error) throw error;
      
      alert(`‚úÖ Customer status updated to ${newStatus.toUpperCase()}`);
      // Reload customers to reflect changes
      await loadCustomers();
    } catch (error) {
      console.error('Error updating customer status:', error);
      alert(`‚ùå Failed to update customer status: ${error.message}`);
    }
  };

  // Handle customer form submission - unified with AdminCustomers
  const handleCustomerSubmit = async (formData) => {
    try {
      if (editingCustomer) {
        // Update existing customer - use direct Supabase update
        const { error } = await supabase
          .from('profiles')
          .update({
            name: formData.name,
            phone: formData.mobile,
            email: formData.email || null,
            state: formData.state,
            city: formData.city,
            pincode: formData.pincode,
            address: formData.address,
            parent_promoter_id: user?.id,
            updated_at: new Date().toISOString()
          })
          .eq('id', editingCustomer.id);
        
        if (error) {
          console.error('‚ùå Database error updating customer:', error);
          throw error;
        }
        
        alert('‚úÖ Customer updated successfully!');
        
        // Reload customers to reflect changes
        await loadCustomers();
        closeModal();
      } else {
        // Create new customer
        if (!formData.name || !formData.mobile || !formData.cardNo) {
          throw new Error('Missing required fields');
        }
        
        // Check if promoter has sufficient pins before creating customer
        if (availablePins < 1) {
          throw new Error(`Insufficient pins to create customer. You need 1 pin but only have ${availablePins} pins available. Please request more pins from admin.`);
        }
        
        const dbParams = {
          p_name: formData.name,
          p_mobile: formData.mobile,
          p_state: formData.state,
          p_city: formData.city,
          p_pincode: formData.pincode,
          p_address: formData.address,
          p_customer_id: formData.cardNo,
          p_password: formData.password,
          p_parent_promoter_id: user?.id, // Current promoter as parent
          p_email: formData.email || null
        };
        
        // Create customer with pin deduction
        const { data: result, error } = await supabase.rpc('create_customer_with_pin_deduction', dbParams);
        
        if (error) {
          console.error('Database error creating customer:', error);
        }
        
        if (error || !result?.success) {
          throw new Error(result?.error || error?.message || 'Failed to create customer');
        }
        
        alert(`‚úÖ Customer created successfully!

üÜî Customer ID: ${formData.cardNo}
üë§ Name: ${formData.name}
üìß Email: ${formData.email || 'Not provided'}
üì± Mobile: ${formData.mobile}
üí∞ Investment Plan: ‚Çπ1000 per month for 20 months
üìå Pin Deducted: 1 from your account (${result.pins_remaining} pins remaining)

Customer can now login using their Customer ID (${formData.cardNo}) and password.
Payment schedule has been automatically created for 20 months.`);
        
        // Reload customers and pins after creation
        await loadCustomers();
        await loadPromoterPins();
        
        closeModal();
      }

    } catch (error) {
      console.error('Error creating/updating customer:', error);
      alert(`Failed to create customer: ${error.message}`);
    }
  };

  const handleDelete = async () => {
    if (deletingCustomer) {
      try {
        // Delete from profiles table (customers are stored there)
        const { error } = await supabase
          .from('profiles')
          .delete()
          .eq('id', deletingCustomer.id);
          
        if (error) throw error;
        
        alert(`‚úÖ Customer "${deletingCustomer.name}" deleted successfully!`);
        await loadCustomers();
        closeDeleteModal();
      } catch (error) {
        console.error('Error deleting customer:', error);
        alert(`‚ùå Failed to delete customer: ${error.message}`);
      }
    }
  };

  // Get promoter name for display (simplified for promoter view)
  const getPromoterName = (customer) => {
    const promoterId = customer?.parent_promoter_id;
    if (!promoterId) return 'Unassigned';
    
    // For promoter view, customers should be assigned to current promoter
    if (promoterId === user?.id) {
      return user?.name || 'You';
    }
    
    return 'Other Promoter';
  };

  // Get promoter system ID for display
  const getPromoterSystemId = (customer) => {
    const promoterId = customer?.parent_promoter_id;
    if (!promoterId || promoterId !== user?.id) return '';
    
    return user?.promoter_id || 'SELF';
  };

  return (
    <>
      <SharedStyles />
      <PromoterNavbar />
      <UnifiedBackground>
        <div className="min-h-screen p-8">
          <div className="max-w-7xl mx-auto">
            {/* Header with Pin Display */}
            <div className="flex justify-between items-center mb-8" data-animate>
              <div>
                <h1 className="text-4xl font-bold text-white mb-2">Customer Management</h1>
                <p className="text-gray-300">Manage your customer accounts and assignments</p>
              </div>
              <div className="flex items-center space-x-4">
                {/* Pin Display */}
                <div className="bg-white rounded-lg p-4 min-w-[200px]">
                  <div className="flex items-center justify-between">
                    <div>
                      <p className="text-sm font-medium text-slate-600">Available Pins</p>
                      <p className={`text-3xl font-bold ${availablePins > 0 ? 'text-slate-900' : 'text-red-500'}`}>
                        {availablePins}
                      </p>
                      <p className="text-xs text-slate-500 mt-1">
                        {availablePins === 0 ? 'No pins available - contact admin' : 'Required: 1 pin per customer'}
                      </p>
                    </div>
                    <div className={`w-12 h-12 rounded-lg flex items-center justify-center ${
                      availablePins > 0 ? 'bg-orange-500' : 'bg-red-500'
                    }`}>
                      <svg className="w-6 h-6 text-white" fill="currentColor" viewBox="0 0 20 20">
                        <path fillRule="evenodd" d="M5 9V7a5 5 0 0110 0v2a2 2 0 012 2v5a2 2 0 01-2 2H5a2 2 0 01-2-2v-5a2 2 0 012-2zm8-2v2H7V7a3 3 0 016 0z" clipRule="evenodd"/>
                      </svg>
                    </div>
                  </div>
                </div>
                <UnifiedButton
                  onClick={openCreateModal}
                  className="flex items-center space-x-2"
                  disabled={availablePins < 1}
                >
                  <Plus className="w-5 h-5" />
                  <span>Create Customer</span>
                </UnifiedButton>
              </div>
            </div>

            {/* Search and Filters */}
            <UnifiedCard className="p-6 mb-8" data-animate>
              <div className="flex flex-col md:flex-row gap-4">
                <div className="flex-1 relative">
                  <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-5 h-5" />
                  <input
                    type="text"
                    placeholder="Search by name, email, phone, or customer ID..."
                    value={searchTerm}
                    onChange={(e) => setSearchTerm(e.target.value)}
                    className="w-full pl-10 pr-4 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white placeholder-gray-400 focus:outline-none focus:ring-2 focus:ring-green-500"
                  />
                </div>
                <div className="flex gap-4">
                  <select
                    value={statusFilter}
                    onChange={(e) => setStatusFilter(e.target.value)}
                    className="px-4 py-2 bg-gray-700 border border-gray-600 rounded-lg text-white focus:outline-none focus:ring-2 focus:ring-green-500"
                  >
                    <option value="">All Status</option>
                    <option value="active">Active</option>
                    <option value="inactive">Inactive</option>
                  </select>
                </div>
              </div>
            </UnifiedCard>

            {/* Customers Table */}
            <UnifiedCard className="overflow-hidden" data-animate>
              
              {loading ? (
                <div className="p-8">
                  <SkeletonFullPage type="customers" />
                </div>
              ) : (
                <div className="overflow-x-auto">
                  <table className="w-full">
                    <thead className="bg-gray-700">
                      <tr>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Customer ID</th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Name</th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Contact Details</th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Investment Plan</th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Assigned Promoter</th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Status</th>
                        <th className="px-6 py-3 text-left text-xs font-medium text-gray-300 uppercase tracking-wider">Actions</th>
                      </tr>
                    </thead>
                    <tbody className="bg-gray-800 divide-y divide-gray-700">
                      {filteredCustomers.length === 0 ? (
                        <tr>
                          <td colSpan="7" className="px-6 py-12 text-center text-gray-400">
                            No customers found
                          </td>
                        </tr>
                      ) : (
                        filteredCustomers.map((customer) => {
                          const customerId = customer?.id;
                          const customerName = customer?.name || 'Unknown';
                          const customerEmail = customer?.email || 'N/A';
                          const customerPhone = customer?.phone || 'N/A';
                          const systemCustomerID = customer?.customer_id || 'N/A';
                          const status = customer?.status || 'active';
                          const promoterName = getPromoterName(customer);
                          const promoterSystemId = getPromoterSystemId(customer);
                          
                          return (
                            <tr key={customerId} className="hover:bg-gray-700">
                              <td className="px-6 py-4 whitespace-nowrap">
                                <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-green-100 text-green-800">
                                  {systemCustomerID}
                                </span>
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap">
                                <div className="flex items-center">
                                  <div className="w-8 h-8 bg-gradient-to-r from-green-400 to-blue-500 rounded-full flex items-center justify-center mr-3">
                                    <Users className="w-4 h-4 text-white" />
                                  </div>
                                  <div className="text-sm font-medium text-white">{customerName}</div>
                                </div>
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-300">
                                <div className="space-y-1">
                                  <div className="flex items-center">
                                    <Mail className="w-3 h-3 mr-2 text-gray-400" />
                                    {customerEmail === 'N/A' ? 'No email' : customerEmail}
                                  </div>
                                  <div className="flex items-center">
                                    <Phone className="w-3 h-3 mr-2 text-gray-400" />
                                    {customerPhone === 'N/A' ? 'No phone' : customerPhone}
                                  </div>
                                </div>
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-300">
                                <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-blue-500/20 text-blue-400">
                                  ‚Çπ1000 √ó 20 months
                                </span>
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-300">
                                <div className="flex items-center">
                                  <Users className="w-4 h-4 mr-2 text-blue-400" />
                                  <div className="flex flex-col">
                                    <span className="text-white font-medium">{promoterName}</span>
                                    {promoterSystemId && (
                                      <span className="text-xs text-gray-400">{promoterSystemId}</span>
                                    )}
                                  </div>
                                </div>
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap">
                                <button
                                  onClick={() => toggleCustomerStatus(customer)}
                                  className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium transition-colors ${
                                    status === 'active' 
                                      ? 'bg-green-100 text-green-800 hover:bg-green-200' 
                                      : 'bg-red-100 text-red-800 hover:bg-red-200'
                                  }`}
                                  title={`Click to ${status === 'active' ? 'deactivate' : 'activate'} customer`}
                                >
                                  {status === 'active' ? '‚úÖ Active' : '‚ùå Inactive'}
                                </button>
                              </td>
                              <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                                <div className="flex space-x-2">
                                  <button
                                    onClick={() => openPaymentManager(customer)}
                                    className="text-green-400 hover:text-green-300 transition-colors"
                                    title="Manage Payments"
                                  >
                                    <CreditCard className="w-4 h-4" />
                                  </button>
                                  <button
                                    onClick={() => openEditModal(customer)}
                                    className="text-orange-400 hover:text-orange-300 transition-colors"
                                    title="Edit Customer"
                                  >
                                    <Edit className="w-4 h-4" />
                                  </button>
                                  <button
                                    onClick={() => openDeleteModal(customer)}
                                    className="text-red-400 hover:text-red-300 transition-colors"
                                    title="Delete Customer"
                                  >
                                    <Trash2 className="w-4 h-4" />
                                  </button>
                                </div>
                              </td>
                            </tr>
                          );
                        })
                      )}
                    </tbody>
                  </table>
                </div>
              )}
            </UnifiedCard>
          </div>
        </div>

        {/* Unified Customer Form */}
        <UnifiedCustomerForm
          isOpen={showModal}
          onClose={closeModal}
          onSubmit={handleCustomerSubmit}
          promoters={[{ 
            id: user?.id, 
            name: user?.name || 'Current Promoter', 
            promoter_id: user?.promoter_id || 'SELF',
            role: 'promoter'
          }]}
          isEditing={!!editingCustomer}
          initialData={editingCustomer}
          currentUserRole="promoter"
        />

        {/* Payment Manager */}
        <PaymentManager
          isOpen={showPaymentManager}
          onClose={closePaymentManager}
          customerId={selectedCustomerForPayments?.id}
          customerName={selectedCustomerForPayments?.name}
          currentUser={user}
        />

        {/* Delete Confirmation Modal */}
        {showDeleteModal && (
          <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
            <div className="bg-gray-800 rounded-lg p-6 w-full max-w-md mx-4">
              <h3 className="text-lg font-semibold text-white mb-4">Confirm Delete</h3>
              <p className="text-gray-300 mb-6">
                Are you sure you want to delete customer "{deletingCustomer?.name}"? This action cannot be undone.
              </p>
              <div className="flex justify-end space-x-3">
                <button
                  onClick={closeDeleteModal}
                  className="px-4 py-2 text-gray-300 hover:text-white transition-colors"
                >
                  Cancel
                </button>
                <button
                  onClick={handleDelete}
                  className="px-4 py-2 bg-red-600 text-white rounded-lg hover:bg-red-700 transition-colors"
                >
                  Delete
                </button>
              </div>
            </div>
          </div>
        )}
      </UnifiedBackground>
    </>
  );
}

export default PromoterCustomers;
